body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: beige;
  display: flex;
  justify-content: center;
  height: 100vh;
}
ul {
    list-style-type: none;
    color: #f2f2f2;
    margin: 0;
    padding: 0;
}
li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    background-color: #424242;
    padding-left: 5px;
    font-family: 'Century Gothic', sans-serif;
    font-size: 13pt;
    padding-top: 2px;
    padding-bottom: 2px;
}

.task-done {
    color: greenyellow;
}

.icon-container {
    display: flex;
    justify-content: center;
    align-items: center;
}
.icon-block {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 4px;
}
.icon-done {
    color: #1f1f1f;
    font-size: 15pt;
    transition: color 0.1s linear;
    cursor: pointer;
}
.icon-done:hover {
    color: greenyellow;
}
.icon-close {
    color: #1f1f1f;
    font-size: 15pt;
    transition: color 0.1s linear;
    cursor: pointer;
}
.icon-close:hover {
    color: crimson;
}
.Tools {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    margin-left: 5px;
    margin-bottom: 7px;
    margin-right: 5px;
}

input {
    font-family: 'Century Gothic', sans-serif;
    font-size: 13pt;
    outline-color: crimson;
}
input[type="text"] {
    width: 60%;
}
input[type="submit"] {
    width: 40%;
    margin-left: 1px;
    border: 0;
    background-color: greenyellow;
    color: #1f1f1f;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    cursor: pointer;
    opacity: 0.8;
    transition: opacity 0.1s linear;
}
input[type="submit"]:hover {
    opacity: 1;
}
.ToDoList {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 35vw;
    min-width: 500px;
    background-color: #1f1f1f;
    margin-top: 30px;
    border-radius: 5px;
    margin-bottom: 30px;
}
